<template>
	<div class="account create_one mt-5 position-relative">
		<b-container>
			<div class="header mt-4">
				<div class="h3 mb-0">{{ $t("editIntro") }}</div>
			</div>
			<b-form @submit.prevent="save" class="mt-5">
				<!-- Apperance -->
				<div class="field_title pt-1 mb-4">
					<div class="title font-weight-500">{{ $t("apperance") }}</div>
				</div>
				<b-row>
					<b-col cols="12" lg="8" class="mb-4">
						<form-input :label="$t('welcomeMessage')" :placeholder="$t('welecomeMessagePlaceholder')" v-model="profile.welcomeMessage" v-visible="'welcomeMessage'">
							<template #icon_left>
								<BIcon icon="chat-left-dots" font-scale="1.4" />
							</template>
						</form-input>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="welcomeMessage">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">{{ $t("welcomeMessageDesc") }}</p>
						</div>
					</b-col>

					<!-- Cover Photo -->
					<b-col cols="12" lg="8" class="mb-4">
						<virtual-input :label="$t('photoCover')" v-visible="'image'" :inputProps="{ class: 'cursor-auto' }">
							<BIcon slot="icon_left" icon="image" />
							<template #default>
								<div class="image_upload_wrapper">
									<div class="btns d-flex" :class="{ has_review: imageReview }">
										<span class="btn_icon btn_icon_md" v-b-tooltip.hover :title="$t('addPhoto')" v-b-modal.ImageModal>
											<CameraPlus width="1.7rem" />
										</span>
										<span v-if="imageReview" class="btn_icon btn_icon_md mx-4" v-b-tooltip.hover :title="$t('removePhoto')" @click="profile.image = null">
											<BIcon icon="trash" scale="1.5" />
										</span>
									</div>
									<img v-if="imageReview" :src="imageReview" alt="Cover" class="w-100 h-100" />
								</div>
							</template>
						</virtual-input>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="image">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">{{ $t("coverPhotoDesc") }}</p>
						</div>
					</b-col>
				</b-row>
			</b-form>
		</b-container>
		<div class="footer_control bg-white border-top w-100">
			<b-container class="h-100">
				<div class="d-flex justify-content-end align-items-center h-100">
					<b-button variant="primary" @click="save" class="px-3"> {{ $t("save") }} </b-button>
				</div>
			</b-container>
		</div>
		<ImageModal v-model="profile.image" />
	</div>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";
	import ImageModal from "@/components/modals/ImageModal";
	import VirtualInput from "@/components/inputs/VirtualInput";
	import CameraPlus from "@/components/icons/cameraPlus";

	export default {
		components: { FormInput, ImageModal, VirtualInput, CameraPlus },

		data() {
			return {
				profile: {
					welcomeMessage: "",
					image: "",
				},
			};
		},

		computed: {
			imageReview() {
				let image = this.profile.image;

				if (!image) return "";

				if (typeof image != "string") {
					// create url from file
					image = URL.createObjectURL(image);
				}

				return image;
			},
		},

		methods: {
			save() {},
		},
	};
</script>
